body {
  margin: 0;
}

error h1 {
  font-size: 40px;
}

error h2 {
  font-size: 20px;
}

a {
  color: #3369f3;
  text-decoration: none;
}

button {
  color: #fff;
  box-shadow: none;
  background: #3369f3;
  border: none;
  border-radius: 8px;
  font-size: 13px;
  font-weight: 500;
  line-height: 37px;
}

button:hover {
  cursor: pointer;
  box-shadow: inset 0 0 5px #000000bf;
}

#auth {
  width: 340px;
  box-sizing: border-box;
  border: 1px solid #eee;
  border-radius: 12px;
  margin: auto;
  padding: 30px;
  position: relative;
  box-shadow: 0 0 6px #00000024;
}

#auth button {
  width: 100%;
  margin-bottom: 14px;
  display: block;
}

.auth__actions {
  margin-top: 47px;
  position: relative;
  bottom: 0;
}

input {
  border: none;
}

input:focus {
  outline: none;
}

input:focus::placeholder {
  color: #0000;
}

.form__input {
  text-align: left;
  margin-top: 32px;
  padding-top: 20px;
  position: relative;
}

.form__input label {
  color: #999;
  font-size: 9px;
  display: none;
  position: absolute;
  top: 0;
}

.form__input input {
  width: 100%;
  border: none;
  border-bottom: 1px solid #3369f3 !important;
}

.form__input input:not(:placeholder-shown) ~ label, .form__input input:focus ~ label {
  display: block;
}

#profile h3 {
  margin: 21px;
  font-size: 16px;
}

#profile #profile__avatar-change {
  width: 100%;
  height: 100%;
  color: #fff;
  background-color: #00000080;
  padding-top: 50px;
  font-size: 13px;
  display: none;
}

#profile #profile__avatar {
  width: 130px;
  height: 130px;
  background: #efefef url("emptyImage.842798ee.png") center no-repeat;
  border-radius: 65px;
  margin: 0 auto;
  overflow: hidden;
}

#profile #profile__avatar:hover #profile__avatar-change {
  display: block;
}

.profile__data {
  margin-top: 60px;
}

.profile__input, .profile__link {
  height: 33px;
  width: 100%;
  text-align: right;
  border-bottom: 1px solid #eaeaea;
  line-height: 33px;
  position: relative;
}

.profile__input label, .profile__link label {
  font-size: 13px;
  display: block;
  position: absolute;
  left: 0;
}

.profile__input input, .profile__link input {
  text-align: right;
  color: #999;
}

.profile__input:last-child, .profile__link:last-child {
  border: none;
}

.redLink a {
  color: red;
}

.profile__controls {
  margin-top: 60px;
  position: relative;
}

.profile__controls .profile__link {
  text-align: left;
}

.profile__controls button {
  width: 280px;
  margin: 0 auto;
  display: block;
}

